  #changeEvent = 'input'
  // å½è¿ç¨/åè°éªè¯å¨è¿åæ¶ï¼æç¤ºè¡¨åå·²åå¤å¥½æäº¤çæ å¿
  #submitIfValid = null
  // ç¼å­çå­æ®µåç´ 
  #cacheFields = {}
